xin display-flex($display: flex) {\n  @include flexbox((display: $display));\n}\n\n// Values: row | row-reverse | column | column-reverse\n@mixin flex-direction($direction) {\n  @include flexbox((flex-direction: $direction));\n}\n\n// Values: nowrap | wrap | wrap-reverse\n@mixin flex-wrap($wrap) {\n  @include flexbox((flex-wrap: $wrap));\n}\n\n// Shorthand for flex-direction and flex-wrap.\n@mixin flex-flow($flow) {\n  @include flexbox((flex-flow: $flow));\n}\n\n// Accepts an integer\n@mixin order($order) {\n  @include flexbox((order: $order));\n}\n\n// Shorthand for flex-grow, flex-shrink and optionally flex-basis.\n// Space separated, in that order.\n@mixin flex($flex) {\n  @include flexbox((flex: $flex));\n}\n\n// Accepts a number.\n@mixin flex-grow($flex-grow) {\n  @include flexbox((flex-grow: $flex-grow));\n}\n\n// Accepts a number.\n@mixin flex-shrink($flex-shrink) {\n  @include flexbox((flex-shrink: $flex-shrink));\n}\n\n// Accepts any legal value for the width property.\n@mixin flex-basis($flex-basis) {\n  @include flexbox((flex-basis: $flex-basis));\n}\n\n// Legal values: flex-start | flex-end | center | space-between | space-around\n@mixin justify-content($justify-content) {\n  @include flexbox((justify-content: $justify-content));\n}\n\n// Legal values: flex-start | flex-end | center | baseline | stretch\n@mixin align-items($align-items) {\n  @include flexbox((align-items: $align-items));\n}\n\n// Legal values: auto | flex-start | flex-end | center | baseline | stretch\n@mixin align-self($align-self) {\n  @include flexbox((align-self: $align-self));\n}\n\n// Legal values: flex-start | flex-end | center | space-between | space-around | stretch\n@mixin align-content($align-content) {\n  @include flexbox((align-content: $align-content));\n}\n","/* Utilities */\n\n.grid-margin {\n  margin-bottom: $card-spacing-y;\n}\n.grid-margin-sm-0 {\n  @media (min-width: 576px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-md-0 {\n  @media (min-width: 768px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-lg-0 {\n  @media (min-width: 992px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-xl-0 {\n  @media (min-width: 1200px) {\n    margin-bottom: 0;\n  }\n}\n.img-lg {\n  width: 92px;\n  height: 92px;\n}\n.img-sm {\n  width: 43px;\n  height: 43px;\n}\n.img-xs {\n  width: 37px;\n  height: 37px;\n}\n.img-ss {\n  width: 26px;\n  height: 26px;\n}\n.stretch-card {\n  @include display-flex;\n  @include align-items(stretch);\n  @include justify-content(stretch);\n  >.card{\n    width: 100%;\n    min-width: 100%;\n  }\n}\n\n.border-right-sm {\n  @media (min-width: 576px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n.border-right-md {\n  @media (min-width: 768px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n.border-right-lg {\n  @media (min-width: 992px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n\n.border-left-sm {\n  @media (min-width: 576px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n.border-left-md {\n  @media (min-width: 768px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n.border-left-lg {\n  @media (min-width: 992px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n\n.text-gray {\n  color: #8c8c8c;\n}\n\n.text-black {\n  color: $black;\n}\n\n.text-red {\n  color: $red;\n}\n\n.text-small {\n  font-size: 12px;\n}\n\n.flex-grow {\n  flex-grow: 1;\n}\n\n.font-weight-light {\n  font-family: $type1-light;\n}\n\n.font-weight-bold {\n  font-family: $type1-bold;\n}\n\n.font-weight-normal {\n  font-family: $type1-regular;\n}\n","/* Demo Styles */\n// Add spacing to Boostrap components for demo purpose\n.template-demo {\n  > .btn {\n    @extend .mt-3;\n    @extend .mr-3;\n  }\n\n  .slider-wrap {\n    height: 100px;\n  }\n\n  > .btn-toolbar {\n    @extend .mt-3;\n    @extend .mr-3;\n  }\n\n  > .btn-group,\n  >  .btn-group-vertical {\n    @extend .mt-2;\n    @extend .mr-2;\n  }\n\n  .progress {\n    margin-top: 1.5rem;\n  }\n\n  .circle-progress {\n    @extend .mt-2;\n  }\n\n  > h2,\n   > h3,\n   > h4,\n   > h5,\n   > h6,\n  > h1 {\n    border-top: 1px solid $border-color;\n    padding: 0.5rem 0 0;\n  }\n\n  .ul-slider {\n    .noUi-horizontal {\n      margin-top: 2rem;\n    }\n\n    .noUi-vertical {\n      margin-right: 2rem;\n    }\n  }\n\n  > .dropdown {\n    display: inline-block;\n    @extend .mr-2;\n    margin-bottom: 0.5rem;\n  }\n\n  nav {\n    .breadcrumb {\n      margin-bottom: 1.375rem;\n    }\n\n    &:last-child {\n      .breadcrumb {\n        margin-bottom: 0;\n      }\n    }\n  }\n\n  .editable-form {\n    > .form-group {\n      border-bottom: 1px solid $border-color;\n      padding-bottom: 0.8rem;\n      margin-bottom: 0.8rem;\n    }\n  }\n\n  .circle-progress {\n    padding: 15px;\n  }\n\n  .circle-progress-block {\n    @extend .mb-3;\n    @extend .px-2;\n  }\n}\n\n.demo-modal {\n  position: static;\n  display: block;\n\n  .modal-dialog {\n    @extend .mt-0;\n\n    &.modal-lg {\n      max-width: 100%;\n    }\n  }\n}\n\n.loader-demo-box {\n  @extend .border;\n  @extend .border-secondary;\n  @extend .rounded;\n  width: 100%;\n  height: 200px;\n  @extend .d-flex;\n  @extend .align-items-center;\n}\n\n.dropdown-menu-static-demo {\n  height: 250px;\n  margin-bottom: 20px;\n}",".main-spinner-wrapper {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  height: 100vh;\n  background: #eee;\n  .donut {\n    width: 4rem;\n    height: 4rem;\n    border-radius: 50%;\n    border: 0.3rem solid rgba(#979fd0, 0.3);\n    border-top-color: #979fd0;\n    animation: 1.5s spin infinite linear;\n  }\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}","/* Forms */\n\n.form-group {\n  margin-bottom: 1.5rem;\n}\n\n.input-group-append,\n.input-group-prepend {\n  color: $input-placeholder-color;\n  width: auto;\n  border: none;\n  .input-group-text {\n    border-color: $border-color;\n    padding: 0.575rem 0.75rem;\n    color: $input-placeholder-color;\n  }\n  button {\n    height: 33.5px;\n    padding-top: .3rem;\n    padding-bottom: .3rem;\n  }\n}\n\n.custom-control {\n  .custom-control-label {\n    line-height: 1.6;\n    margin-bottom: 0;\n  }\n}\n\n.form-control {\n  border: 1px solid $border-color;\n  font-family: $type1-regular;\n  font-size: $input-font-size;\n}\n\nselect {\n  &.form-control {\n    padding: .4375rem .75rem;\n    border: 0;\n    outline: 1px solid $border-color;\n    color: $input-placeholder-color;\n    &:focus {\n      outline: 1px solid $border-color;\n    }\n    @each $color,\n    $value in $theme-colors {\n      &.border-#{$color} {\n        outline: 1px solid $value;\n        &:focus {\n          outline: 1px solid $value;\n        }\n      }\n    }\n  }\n}\n\n.form-group {\n  label {\n    font-size: $default-font-size;\n    line-height: 1;\n    vertical-align: top;\n    margin-bottom: .5rem;\n  }\n  &.has-danger {\n    .form-control {\n      border-color: theme-color(danger);\n    }\n  }\n  .file-upload-default {\n    visibility: hidden;\n    position: absolute;\n  }\n  .file-upload-info {\n    background: transparent;\n  }\n}\n","/* Checkboxes and Radios */\n\n.form-check {\n  position: relative;\n  display: block;\n  margin-top: 10px;\n  margin-bottom: 10px;\n  padding-left: 0;\n\n  .form-check-label {\n    min-height: 18px;\n    display: block;\n    margin-left: 1.75rem;\n    font-size: $default-font-size;\n    line-height: 1.5;\n    .rtl & {\n      margin-left: 0;\n      margin-right: 1.75rem;\n    }\n\n    input {\n      position: absolute;\n      top: 0;\n      left: 0;\n      .rtl & {\n        left: auto;\n        right: 0;\n      }\n      margin-left: 0;\n      margin-top: 0;\n      z-index: 1;\n      cursor: pointer;\n      opacity: 0;\n      filter: alpha(opacity=0);\n    }\n    input[type=\"checkbox\"] {\n      + .input-helper {\n        &:before,\n        &:after {\n          position: absolute;\n          top: 0;\n          left: 0;\n          .rtl & {\n            left: auto;\n            right: 0;\n          }\n        }\n        &:before {\n          content: \"\";\n          width: 18px;\n          height: 18px;\n          border-radius: 2px;\n          border: solid theme-color(primary);\n          border-width: 2px;\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n        }\n  \n        &:after {\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n          font-family: 'Material Design Icons';\n          opacity: 0;\n          filter: alpha(opacity=0);\n          -webkit-transform: scale(0);\n          -ms-transform: scale(0);\n          -o-transform: scale(0);\n          transform: scale(0);\n          content: '\\F12C';\n          font-size: .9375rem;\n          font-weight: bold;\n          color: $white;\n        }\n      }\n      &:checked {\n        +.input-helper {\n          &:before {\n            background: theme-color(primary);\n            border-width: 0;\n          }\n          &:after {\n            width: 18px;\n            opacity: 1;\n            line-height: 18px;\n            filter: alpha(opacity=100);\n            -webkit-transform: scale(1);\n            -ms-transform: scale(1);\n            -o-transform: scale(1);\n            transform: scale(1);\n          }\n        }\n      }\n\n      &:disabled {\n        + .input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          + .input-helper {\n            &:after {\n              background: $border-color;\n              color: $white;\n            }\n          }\n        }\n      }\n    }\n    input[type=\"radio\"] {\n      +.input-helper {\n        &:before {\n          position: absolute;\n          content: \"\";\n          top: 0;\n          left: 0;\n          .rtl & {\n            left: auto;\n            right: 0;\n          }\n          border: solid theme-color(primary);\n          border-width: 2px;\n          width: 20px;\n          height: 20px;\n          border-radius: 50%;\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n        }\n\n        &:after {\n          content: \"\";\n          width: 8px;\n          height: 8px;\n          background: $white;\n          border-radius: 50%;\n          top: 6px;\n          left: 6px;\n          .rtl & {\n            left: auto;\n            right: 6px;\n          }\n          -webkit-transition: all;\n          -o-transition: all;\n          transition: all;\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n          opacity: 0;\n          filter: alpha(opacity=0);\n          -webkit-transform: scale(0);\n          -ms-transform: scale(0);\n          -o-transform: scale(0);\n          transform: scale(0);\n          position: absolute;\n        }\n      }\n\n      &:checked {\n        +.input-helper {\n          &:before {\n            background: theme-color(primary);\n            border-width: 0;\n          }\n\n          &:after {\n            opacity: 1;\n            line-height: 1.5;\n            filter: alpha(opacity=100);\n            -webkit-transform: scale(1);\n            -ms-transform: scale(1);\n            -o-transform: scale(1);\n            transform: scale(1);\n          }\n        }\n      }\n\n      &:disabled {\n        + .input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          + .input-helper {\n            &:before {\n              background: $border-color;\n            }\n\n            &:after {\n              background: $white;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .form-check-#{$color} {\n    &.form-check {\n      label {\n        input[type=\"checkbox\"],\n        input[type=\"radio\"] {\n          +.input-helper {\n            &:before {\n              border-color: theme-color($color);\n            }\n          }\n          &:checked {\n            +.input-helper {\n              &:before {\n                background: $value;\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (min-width: 576px) {\n  .form-inline .form-check {\n\n  }\n}","/* Icons */\n\n.icons-list {\n  border-left: 1px solid $border-color;\n  border-bottom: 1px solid $border-color;\n  >div {\n    background: $white;\n    border-top: 1px solid $border-color;\n    border-right: 1px solid $border-color;\n    @include display-flex;\n    @include align-items(center);\n    padding:15px 15px;\n    font-family: $type1-regular;\n    font-size: $default-font-size;\n\n    i {\n      display: inline-block;\n      font-size: 20px;\n      width: 40px;\n      text-align: left;\n      color: theme-color(primary);\n    }\n  }\n}\n","/* Tables */\n\n.table {\n  margin-bottom: 0;\n  thead {\n    th {\n      border-top: 0;\n      border-bottom-width: 1px;\n      font-family: $type1-medium;\n      font-weight: initial;\n      i{\n        margin-left: 0.325rem;\n      }\n    }\n  }\n  th,\n  td {\n    vertical-align: middle;\n    font-size: $default-font-size;\n    line-height: 1;\n    img {\n      width: 36px;\n      height: 36px;\n      border-radius: 100%;\n    }\n    .badge {\n      margin-bottom: 0;\n    }\n  }\n  &.table-borderless{\n    border: none;\n    tr,td,th{\n      border: none;\n    }\n  }\n}\n","/* Accordions */\n\n.accordion {\n  .card {\n    margin-bottom: .75rem;\n    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);    \n    border-radius: .25rem;\n    .card-header {\n      background-color: transparent;\n      border: none;\n      padding: 2rem;\n      * {\n        font-weight: 400;\n        font-size: 1rem;        \n      }\n      button {\n        padding: 0;\n        display: block;\n        width: 100%;\n        text-align: left;\n        color: inherit;\n        text-decoration: none;\n        font-size: inherit;\n        position: relative;\n        @include transition(color .5s ease);\n        padding-right: 1.5rem;\n        &:before {\n          font-family:\"Material Design Icons\";\n          position: absolute;\n          right: 7px;\n          top: 0;\n          font-size: 18px;\n          display: block;\n        }\n        &[aria-expanded=\"true\"] {\n          color: theme-color(primary);\n          &:before {\n            content: \"\\F0374\";\n          }\n        }\n        &[aria-expanded=\"false\"] {\n          &:before {\n            content: \"\\F0415\";\n          }\n        }\n      }\n    }\n    .card-body {\n      font-size: 14px;\n      padding: 0 2rem 2rem 2rem;\n      font-weight: 400;\n      line-height: 1.5;\n      i {\n        font-size: 1.25rem;\n      }\n    }\n  }\n  &.accordion-bordered {\n    background: $white;\n    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);    \n    .card {\n      margin: 0 2rem;\n      border-top: 1px solid $border-color;\n      box-shadow: none;\n      border-radius: 0;\n      border-left: 0;\n      border-right: 0;\n      .card-header,\n      .card-body {\n        padding-left: 0;\n        padding-right: 0;\n      }\n      .card-header {\n        button {\n          &:before {\n            color: theme-color(danger);\n          }\n          &[aria-expanded=\"true\"] {\n            color: inherit;\n            &:before {\n              content: \"\\F0062\";\n            }\n          }\n          &[aria-expanded=\"false\"] {\n            &:before {\n              content: \"\\F004A\";\n            }\n          }\n        }\n      }\n      &:first-child {\n        border-top: 0;\n      }\n      &:last-child {\n        border-bottom: 0;\n      }\n    }\n  }\n  &.accordion-filled {\n    .card {\n      padding: 0;\n      .card-header {\n        padding: 0;\n        button {\n          padding: 2rem 4.5rem 2rem 2rem;\n          @include transition(all .2s linear);\n          &:before {\n            top: 40%;\n            right: 40px;\n          }\n          &[aria-expanded=\"true\"] {\n            background: theme-color(info);\n            color: $white;\n            &:before {\n              content: \"\\F0143\";\n              color: $white;\n            }\n          }\n          &[aria-expanded=\"false\"] {\n            &:before {\n              content: \"\\F0140\";\n            }\n          }\n        }\n      }\n      .card-body {\n        padding: 0 2rem 2rem 2rem;\n        background: theme-color(info);\n        color: $white;\n      }\n    }\n  }\n  &.accordion-solid-header {\n    .card {\n      padding: 0;\n      .card-header {\n        padding: 0;        \n        button {\n          padding: 2rem 4.5rem 2rem 2rem;\n          @include transition(all .2s linear);\n          &:before {\n            top: 40%;\n            right: 40px;\n          }\n          &[aria-expanded=\"true\"] {\n            background: theme-color(primary);\n            color: $white;\n            &:before {\n              content: \"\\F0143\";\n              color: $white;\n            }\n          }\n          &[aria-expanded=\"false\"] {\n            &:before {\n              content: \"\\F0140\";\n            }\n          }\n        }\n      }\n      .card-body {\n        padding: 2rem;\n      }\n    }\n  }\n  &.accordion-solid-content {\n    .card {\n      padding: 0;\n      .card-header {\n        padding: 0;        \n        button {\n          padding: 2rem 4.5rem 2rem 2rem;\n          @include transition(all .2s linear);\n          &:before {\n            top: 40%;\n            right: 40px;\n          }\n          &[aria-expanded=\"true\"] {\n            color: inherit;\n            &:before {\n              content: \"\\F0143\";\n            }\n          }\n          &[aria-expanded=\"false\"] {\n            &:before {\n              content: \"\\F0140\";\n            }\n          }\n        }\n      }\n      .card-body {\n        padding: 2rem;\n        background: theme-color(success);\n        color: $white;\n      }\n    }\n  }\n  &.accordion-multi-colored {\n    .card {\n      .card-header,\n      .card-body {\n        background: transparent;\n        color: $white;\n      }\n      &:nth-child(1) {\n        background: theme-color(info);\n      }\n      &:nth-child(2) {\n        background: theme-color(success);\n      }\n      &:nth-child(3) {\n        background: theme-color(danger);\n      }\n      &:nth-child(4) {\n        background: theme-color(warning);\n      }\n      &:nth-child(5) {\n        background: theme-color(info);\n      }\n      .card-header {\n        button {\n          &[aria-expanded=\"true\"] {\n            color: inherit;\n            &:before {\n              content: \"\\F0143\";\n            }\n          }\n          &[aria-expanded=\"false\"] {\n            &:before {\n              content: \"\\F0140\";\n            }\n          }\n        }\n      }\n    }\n  }\n}\n","/* Buttons */\n\n.btn {\n  font-size: $btn-font-size;\n  line-height: 1;\n  font-family: $type1-bold;\n  margin-right: 3px;\n  i {\n    font-size: 1rem;\n  }\n  &.btn-rounded {\n    @include border-radius(50px);\n  }\n  &.btn-fw {\n    min-width: $button-fixed-width;\n  }\n  &.btn-sm {\n    font-size: $btn-font-size-sm;\n  }\n  &.btn-lg {\n    font-size: $btn-font-size-lg;\n  }\n  &.btn-xs {\n    padding: $btn-padding-y-xs $btn-padding-x-xs;\n    font-size: $btn-font-size-xs;\n  }\n  /* Buttons with only icons */\n  &.btn-icon {\n    width: 42px;\n    height: 42px;\n    padding: 0;\n  }\n  /* Buttons with icon and text */\n  &.btn-icon-text {\n    .btn-icon-prepend {\n      margin-right: .5rem;\n    }\n    .btn-icon-append {\n      margin-left: .5rem;\n    }\n  }\n  &.btn-social-icon {\n    width: 50px;\n    height: 50px;\n    padding: 0;\n  }\n}\n\n.btn-group {\n  .btn {\n    margin-right: 0;\n    + .btn {\n      border-left: 0;\n    }\n  }\n}\n\n.btn-toolbar {\n  .btn-group {\n    +.btn-group {\n      @extend .ml-2;\n    }\n  }\n}\n/*social buttons*/\n@each $color, $value in $social-colors {\n  .btn-#{$color} {\n    @include social-button(social-color($color));\n  }\n  .btn-outline-#{$color} {\n    @include social-outline-button(social-color($color));\n  }\n}\n/* inverse buttons */\n@each $color, $value in $theme-colors {\n  .btn-inverse-#{$color} {\n    @include button-inverse-variant($value);\n  }\n  .btn-#{$color}:not(.btn-light) {\n    color: $white;\n    &:hover,\n    &:focus,\n    &:active {\n      color: $white;\n    }\n    &:focus,\n    &:active {\n      background: $value;\n      border-color: $value;\n    }\n  }\n  .btn-outline-#{$color} {\n    &:hover,\n    &:focus,\n    &:active {\n      background: theme-gradient-color($color);\n      color: $white;\n    }\n  }\n}\n/* gradient buttons */\n@each $color, $value in $theme-gradient-colors {\n  .btn-gradient-#{$color} {\n    @include button-gradient-variant($value);\n  }\n}","@mixin social-button($color) {\n  background: $color;\n  color: $white;\n\n  &:hover,\n  &:focus {\n    background: darken($color, 10%);\n    color: $white;\n  }\n  &.btn-social-icon-text {\n    padding: 0 1.5rem 0 0;\n    background: lighten($color, 10%);\n    i {\n      background: $color;\n      padding: .75rem;\n      display: inline-block;\n      margin-right: 1.5rem;\n    }\n  }\n}\n@mixin social-outline-button($color) {\nborder: 1px solid $color;\ncolor: $color;\n&:hover {\n  background: $color;\n  color: $white;\n}\n}\n@mixin button-inverse-variant($color, $color-hover: $white) {\n  background-color: rgba($color, 0.2);\n  background-image: none;\n  border-color: rgba($color, 0);\n  &:not(.btn-inverse-light) {\n    color: $color;      \n  }\n  @include hover {\n    color: $color-hover;\n    background-color: $color;\n    border-color: $color;\n  }\n\n  &.focus,\n  &:focus {\n    box-shadow: 0 0 0 3px rgba($color, .5);\n  }\n\n  &.disabled,\n  &:disabled {\n    color: $color;\n    background-color: transparent;\n  }\n\n  &.active,\n  &:active,\n  .show > &.dropdown-toggle {\n    color: $color-hover;\n    background-color: $color;\n    border-color: $color;\n  }\n}\n@mixin button-gradient-variant($value) {\n  background: $value;\n  border: 0;\n  &:not([disabled]):not(.disabled):active,\n  &:not([disabled]):not(.disabled).active,\n  .show > &.dropdown-toggle {\n    background: $value;\n  }\n  &:hover {\n    opacity: .8;\n  }\n  &:not(.btn-gradient-light) {\n    color: $white;      \n    &:hover,\n    &:focus,\n    &:active {\n      color: $white;\n    }\n  }\n  @include transition(opacity .3s ease);\n}\n","/* Breadcrumbs */\n\n.breadcrumb {\n  border: $border-width solid $border-color;\n\n  .breadcrumb-item {\n    font-size: $breadcrumb-font-size;\n  }\n\n  &.breadcrumb-custom {\n    padding: $breadcrumb-custom-padding-y $breadcrumb-custom-padding-x;\n    border-color: $breadcrumb-item-bg;\n    .breadcrumb-item {\n      font-size: $breadcrumb-font-size;\n      background: $breadcrumb-item-bg;\n      padding: $breadcrumb-custom-item-padding-y $breadcrumb-custom-item-padding-x;\n      color: $breadcrumb-custom-item-color;\n      display: inline-block;\n      vertical-align: top;\n      &:last-child {\n        background: transparent;\n      }\n      &:before{\n        content: \"\";\n      }\n      a{\n        position: relative;\n        color: inherit;\n        border: 1px solid $breadcrumb-item-bg;\n        display: inline-block;\n        vertical-align: top;\n        &:before,&:after{\n          position: absolute;\n          top: -9px;\n          width: 0;\n          height: 0;\n          content: \"\";\n          border-top: 21px solid transparent;\n          border-bottom: 21px solid transparent;\n        }\n        &:before{\n          right: -22px;\n          z-index: 3;\n          border-left-color: $breadcrumb-item-bg;\n          border-left-style: solid;\n          border-left-width: 12px;\n        }\n        &:after{\n          border-top: 21px solid transparent;\n          border-bottom: 22px solid transparent;\n          border-left: 12px solid $white;\n          top: -9px;\n          right: -23px;\n        }\n      }\n      span{\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n  }\n\n  &.bg-success,&.bg-dark,&.bg-danger,&.bg-warning,&.bg-primary,&.bg-info{\n    border: none;\n    .breadcrumb-item{\n      color: $white;\n      &:before{\n        color: inherit;\n      }\n      a,span{\n        color: inherit;\n      }\n    }\n  }\n}\n\n\n/* inverse breadcrumb */\n@each $color, $value in $theme-colors {\n  .bg-inverse-#{$color} {\n    @include breadcrumb-inverse-variant($value);\n  }\n}\n","// Color Functions Mixins\n\n// Solid tab variations\n@mixin tab-solid-variant($color) {\n  .nav-link {\n    &.active {\n      background: $color;\n    }\n  }\n}\n// Minimal tab variations\n@mixin tab-minimal-variant($color) {\n  .nav-tabs {\n    .nav-item {\n      .nav-link {\n        &.active {\n          color: $color;\n        }\n      }\n    }\n  }\n}\n// Sider color variations\n@mixin slider-color-variant($color) {\n  .noUi-base {\n    .noUi-connect {\n      background: $color;\n    }\n\n    .noUi-origin {\n      background: $color;\n\n      .noUi-handle {\n        &:after {\n          background: $color;\n        }\n\n        .noUi-tooltip {\n          background: $color;\n          color: $white;\n        }\n      }\n    }\n  }\n}\n// Breadcrumb color variations\n@mixin breadcrumb-inverse-variant($color) {\n  background: lighten($color, 25%);\n  border-color: $color;\n\n  .breadcrumb-item {\n    color: $color;\n\n    &:before {\n      color: inherit;\n    }\n\n    a {\n      color: inherit;\n    }\n  }\n\n  &.breadcrumb-custom {\n    background: transparent;\n\n    .breadcrumb-item {\n      background: lighten($color, 25%);\n\n      a {\n        color: $color;\n        border: none;\n\n        &:before {\n          border-left-color: lighten($color, 25%);\n        }\n      }\n\n      span {\n        color: $color;\n      }\n\n      &:last-child {\n        background: transparent;\n      }\n    }\n  }\n}\n// Pagination variations\n@mixin pagination-variants($color) {\n  .page-item {\n    &.active {\n      .page-link {\n        background: $color;\n        border-color: $color;\n      }\n    }\n\n    .page-link {\n      &:hover {\n        background: lighten($color,5%);\n        border-color: $color;\n        color: $white;\n      }\n    }\n  }\n}\n// Accordion Inverse variations\n@mixin accordion-inverse-variant($color) {\n  .card {\n    background: rgba($color, 0.16);\n    color: $color;\n    border: none;\n\n    .card-header {\n      color: $color;\n      border: none;\n\n      a,\n      h1,\n      h2,\n      h3,\n      h4,\n      h5,\n      h6,\n      p,\n      span {\n        color: $color !important;\n      }\n\n      a {\n        border-color: $color;\n\n        &:before {\n          color: $color !important;\n        }\n\n        &[aria-expanded=\"true\"] {\n          border-bottom: 1px solid rgba($color, 0.16);\n        }\n\n        &[aria-expanded=\"false\"] {\n          border-bottom: 1px solid transparent;\n        }\n      }\n    }\n  }\n}\n","/* Badges */\n\n.badge {\n  border-radius: .125rem;\n  font-size: 11px;\n  font-weight: initial;\n  line-height: 1;\n  padding: .375rem .5625rem;\n  font-family: $type1-medium;\n  &.badge-pill {\n    border-radius: 10rem;\n  }\n}\n\n\n/*Badge variations*/\n@each $color, $value in $theme-colors {\n  .badge-#{$color} {\n    @include badge-variations($value);\n  }\n}\n\n/*Badge gradient variations*/\n@each $color, $value in $theme-gradient-colors {\n  .badge-gradient-#{$color} {\n    @include badge-gradient-variations($value);\n  }\n}\n\n/*Badge outlined variations*/\n@each $color, $value in $theme-colors {\n  .badge-outline-#{$color} {\n    @include badge-outline-variations($value);\n  }\n}","//Badge Mixins\n\n//Badge vriations\n@mixin badge-variations($color) {\n\tborder: 1px solid $color;\n\tcolor: $white;\n}\n// Badge outlined variations\n@mixin badge-outline-variations($color) {\n\tcolor: $color;\n\tborder: 1px solid $color;\n}\n// Badge outlined variations\n@mixin badge-gradient-variations($color) {\n\tbackground: $color;\n\tcolor: $white;\n}","/* Cards */\n\n.card {\n  border: 0;\n  .card-body {\n    padding: 1rem;\n    + .card-body {\n      padding-top: 1rem;\n    }\n  }\n  .card-title {\n    color: $white;\n    margin-bottom: .75rem;\n    text-transform: capitalize;\n    font-family: $type1-medium;\n    font-size: 1.125rem;\n  }\n  .card-subtitle {\n    @extend .text-gray;\n    font-family: $type1-regular;\n    margin-top: 0.625rem;\n    margin-bottom: 0.625rem;\n  \n  }\n  .card-description {\n    margin-bottom: 1.5rem;\n    font-family: $type1-regular;\n    color: $card-description-color;\n  }\n  &.card-outline-success {\n\t\tborder: 1px solid theme-color(\"success\");\n\t}\n\t&.card-outline-primary {\n\t\tborder: 1px solid theme-color(\"primary\");\n\t}\n\t&.card-outline-warning {\n\t\tborder: 1px solid theme-color(\"warning\");\n\t}\n\t&.card-outline-danger {\n\t\tborder: 1px solid theme-color(\"danger\");\n\t}\n\t&.card-rounded {\n\t\t@include border-radius(5px);\n\t}\n\n  &.card-faded {\n    background: #b5b0b2;\n    border-color: #b5b0b2;\n  }\n  &.card-circle-progress {\n    color: $white;\n    text-align: center;\n  }\n  &.card-img-holder {\n    position: relative;\n    .card-img-absolute {\n      position: absolute;\n      top:0;\n      right: 0;\n      height: 100%;\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .card-inverse-#{$color} {\n    @include card-inverse-variant(rgba(theme-color($color), .2), theme-color-level($color, 1), theme-color-level($color, 3));\n  }\n}\n","// Cards Mixins \n\n@mixin card-inverse-variant($bg, $border, $color) {\n  background: $bg;\n  border: 1px solid $border;\n  color: $color;\n}\n","/* Preview */\n\n.preview-list {\n  .preview-item {\n    @include display-flex;\n    @include flex-direction(row);\n    @include align-items(flex-start);\n    padding: .75rem 1.5rem;\n    font-size: .875rem;\n    &:last-child {\n      border-bottom: 0;\n    }\n    &:hover {\n      background: $dropdown-link-hover-bg;\n    }\n    .form-check {\n      margin-top: 8px;\n      margin-right: 1rem;\n    }\n    .preview-thumbnail {\n      color: color(white);\n      position: relative;\n      img,\n      .preview-icon {\n        width: 36px;\n        height: 36px;\n        border-radius: 100%;\n      }\n      .preview-icon {\n        padding: 6px;\n        text-align: center;\n        @include display-flex;\n        @include align-items(center);\n        @include justify-content(center);\n        i {\n          font-size: 1.125rem;\n          margin: 0;\n          .rtl & {\n            @extend .ml-0;\n          }\n        }\n      }\n      .badge {\n        border: 2px solid color(white);\n        border-radius: 100%;\n        bottom: 5px;\n        display: block;\n        height: 14px;\n        left: -5px;\n        padding: 0;\n        position: absolute;\n        width: 14px;\n        &.badge-online {\n          @extend .badge-success;\n        }\n        &.badge-offline {\n          @extend .badge-info;\n        }\n        &.badge-busy {\n          @extend .badge-warning;\n        }\n      }\n    }\n    .preview-item-content {\n      line-height: 1;\n      padding-left: 15px;\n      &:first-child {\n        padding-left: 0;\n      }\n      p {\n        margin-bottom: 10px;\n        .content-category {\n          font-family: 'source-sans-pro-semibold', sans-serif;\n          padding-right: 15px;\n          border-right: 1px solid $border-color;\n          @extend .text-muted;\n        }\n      }\n      .rtl & {\n        padding-left: 0;\n        padding-right: 1rem;\n        margin-right: 0;\n        margin-left: auto;\n      }\n    }\n    .preview-actions {\n      @include display-flex;\n      @include flex-direction(row);\n      i {\n        width: 29px;\n        color: color(gray-lightest);\n        height: 29px;\n        border: 2px solid color(gray-lightest);\n        border-radius: 100%;\n        padding: 3px 6px;\n        display: inline-block;\n        &:first-child {\n          margin-right: 10px;\n        }\n      }\n    }\n  }\n  &.comment-preview {\n    .preview-item {\n      padding: .87rem 0;\n      &:first-child {\n        padding-top: 0;\n      }\n      p {\n        line-height: 27px;\n      }\n    }\n  }\n  &.bordered {\n    .preview-item {\n      border-bottom: 1px solid $border-color;\n      &:last-child {\n        border-bottom: 0;\n      }\n    }\n  }\n}\n","/* Tooltips */\n\n.tooltip-static-demo {\n\t.tooltip {\n\t\tposition: relative;\n\t\tdisplay: inline-block;\n\t\topacity: 1;\n\t\tmargin: 0 10px 10px 0;\n\t}\n\t.bs-tooltip-bottom-demo,\n\t.bs-tooltip-top-demo {\n\t\t.arrow {\n\t\t\tleft: 50%;\n\t\t}\n\t}\n\t.bs-tooltip-right-demo,\n\t.bs-tooltip-left-demo {\n\t\t.arrow {\n\t\t\t&:before {\n\t\t\t\ttop: calc((100%-#{$tooltip-arrow-width})/2);\t\t\t\t\n\t\t\t}\n\t\t}\n\t}\n}\n\n.tooltip {\n\tfont-size: $tooltip-font-size;\n\tmin-width: 5.625rem;\n\t.tooltip-inner {\n\t\tfont-family: $type1-regular;\n\t}\n\tz-index: 1029;\n}\n\n@each $color, $value in $theme-colors {\n\t.tooltip-#{$color} {\n\t  @include tooltip-variant($color);\n\t}\n}\n  ","// Tooltip color  variations\n\n@mixin tooltip-variant($color) {\n\t.tooltip-inner {\n\t\tbackground:  theme-gradient-color($color);\n\t\tcolor: $white;\n\t}\n\t&.bs-tooltip-top {\n    .arrow::before {\n      border-top-color: theme-color($color);\n    }\n  }\n  &.bs-tooltip-right {\n    .arrow::before {\n      border-right-color: theme-color($color);\n    }\n  }\n  &.bs-tooltip-bottom {\n    .arrow::before {\n      border-bottom-color: theme-color($color);\n    }\n  }\n  &.