color: $popover-arrow-color;\n\t\t\t}\n\t\t}\n\t}\n\t&.top {\n\t\t.arrow {\n\t\t\t&:before {\n\t\t\t\tborder-top-color: $popover-border-color;\n\t\t\t}\n\t\t\t&:after {\n\t\t\t\tborder-top-color: $popover-arrow-color;\n\t\t\t}\n\t\t}\n\t}\n\t&.bottom {\n\t\t.arrow {\n\t\t\t&:before {\n\t\t\t\tborder-bottom-color: $popover-border-color;\n\t\t\t}\n\t\t\t&:after {\n\t\t\t\tborder-bottom-color: $popover-arrow-color;\n\t\t\t}\n\t\t}\n\t}\n}\n\n@each $color, $value in $theme-colors {\n\t.popover-#{$color} {\n\t  @include popover-variant($color);\n\t}\n}\n","// popover color variations //\n\n@mixin popover-variant($color) {\n\tborder-color: $color;\n\t.popover-header {\n\t\tbackground: theme-gradient-color($color);\n\t\tcolor: $blue;\n\t\tborder-bottom: 0;\n\t}\n\t&.bs-popover-top {\n\t\t.arrow {\n\t\t\t&:before,\n\t\t\t&:after {\n\t\t\t\tborder-top-color: theme-color($color);\n\t\t\t}\n\t\t}\n\t}\n\t&.bs-popover-right {\n\t\t.arrow {\n\t\t\t&:before,\n\t\t\t&:after {\n\t\t\t\tborder-right-color: theme-color($color);\n\t\t\t}\n\t\t}\n\t}\n\t&.bs-popover-bottom {\n\t\t.arrow {\n\t\t\t&:before,\n\t\t\t&:after {\n\t\t\t\tborder-bottom-color: theme-color($color);\n\t\t\t}\n\t\t}\n\t}\n\t&.bs-popover-left {\n\t\t.arrow {\n\t\t\t&:before,\n\t\t\t&:after {\n\t\t\t\tborder-left-color: theme-color($color);\n\t\t\t}\n\t\t}\n\t}\n}","/* Lists */\n\nul,\nol,\ndl {\n  padding-left: 1rem;\n  font-size: $default-font-size;\n  li {\n    line-height: 1.8;\n  }\n}\n\n.list-ticked,\n.list-arrow,\n.list-star {\n  list-style: none;\n  padding: 0;\n  li {\n    padding-left: 1.5rem;\n    &:before {\n      font-family: \"Material Design Icons\";\n      margin-left: -1.5rem;\n      width: 1.5rem;\n      margin-right: .5rem;\n    }\n  }\n}\n\n.list-ticked {\n  li {\n    &:before {\n      content: '\\F12D';\n      color: theme-color(danger);\n    }\n  }\n}\n\n.list-arrow {\n  li {\n    &:before {\n      content: '\\F142';\n      color: theme-color(success);\n    }\n  }\n}\n\n.list-star {\n  li {\n    &:before {\n      content: '\\F4CE';\n      color: theme-color(warning);\n    }\n  }\n}\n\n.gradient-bullet-list {\n  padding-left: 0;\n  .rtl & {\n    padding-right: 0;\n  }\n  li {\n    position: relative;\n    list-style-type: none;\n    padding-left: 25px;\n    line-height: 1;\n    padding-bottom: 25px;\n    &:before,\n    &:after {\n      content:\"\";\n      position: absolute;\n    }\n    &:before {\n      top: 0;\n      left: 0;\n      width: 15px;\n      height: 15px;\n      border-radius: 100%;\n    }\n    &:after {\n      width: 11px;\n      height: 11px;\n      top: 2px;\n      left: 2px;\n      background: $white;\n      border-radius: 100%;\n    }\n    &:nth-child(1) {\n      &:before {\n        background: theme-gradient-color(danger);        \n      }\n    }\n    &:nth-child(2) {\n      &:before {\n        background: theme-gradient-color(info);        \n      }\n    }\n    &:nth-child(3) {\n      &:before {\n        background: theme-gradient-color(warning);        \n      }\n    }\n    &:nth-child(4) {\n      &:before {\n        background: theme-gradient-color(success);        \n      }\n    }\n    &:nth-child(5) {\n      &:before {\n        background: theme-gradient-color(primary);        \n      }\n    }\n  }\n}","/* Bootstrap Progress */\n\n.progress {\n  @include border-radius(3px);\n  height: 8px;\n  .progress-bar {\n    @include border-radius(3px);\n  }\n\n  &.progress-sm{\n    height: 0.375rem;\n  }\n  &.progress-md {\n    height: 8px;\n  }\n  &.progress-lg {\n    height: 15px;\n  }\n  &.progress-xl {\n    height: 18px;\n  }\n}","/* Bootstrap Alerts */\n\n.alert {\n  font-size: $default-font-size;\n  padding: 0.4rem 1rem;\n  i {\n    font-size: 1.25rem;\n    margin-right: 1.25rem;\n    vertical-align: middle;\n    line-height: .5;\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .alert-#{$color} {\n    @include alert-variant(rgba(theme-color($color), .2), theme-color-level($color, 1), theme-color-level($color, 3));\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .alert-fill-#{$color} {\n    @include alert-variant(theme-color($color), theme-color($color), color(white));\n  }\n}\n",".nav-tabs {\n\t.nav-link {\n\t\tbackground: $nav-tabs-link-bg;\n\t\tcolor: $nav-tabs-link-color;\n\t\tborder-radius: 0;\n\t\tborder: 1px solid $border-color;\n\t\tpadding: .75rem 1.5rem;\n\t}\n\t.nav-item {\n\t\t&:first-child {\n\t\t\t.nav-link {\n\t\t\t\tborder-radius: 4px 0 0 0;\n\t\t\t}\t\n\t\t}\n\t\t&:last-child {\n\t\t\t.nav-link {\n\t\t\t\tborder-radius: 0 4px 0 0;\n\t\t\t}\n\t\t}\n\t}\n}\n\n.nav-pills {\n\tborder-bottom: 1px solid $border-color;\n\tpadding-bottom: 1rem;\n\t.nav-link {\n\t\tborder: 1px solid $border-color;\n\t\tpadding: .5rem 2rem;\n\t}\n\t.nav-item {\n\t\tmargin-right: 1rem;\n\t}\n}\n.pills-vertical {\n\t@extend .row;\n\t.nav-pills {\n\t\t@extend .col-4;\n\t\t@extend .flex-column;\n\t\tborder-bottom: 0;\n\t\t.nav-item {\n\t\t\tmargin-right: 0;\n\t\t\tmargin-bottom: 1rem;\n\t\t\t.nav-link {\n\t\t\t\tpadding: .5rem 1.5rem;\n\t\t\t}\n\t\t}\n\t}\n\t.tab-content {\n\t\t@extend .col-8;\n\t\tborder-top: 1px solid $border-color;\n\t}\n}\n\n/* gradient pills */\n@each $color, $value in $theme-colors {\n  \t.pills-#{$color} {\n\t\t.nav-pills {\n\t\t\t.nav-link {\n\t\t\t\tcolor: $value;\n\t\t\t\t&.active {\n\t\t\t\t\tbackground: theme-gradient-color($color);\n\t\t\t\t\tcolor: $white;\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n}\n\n\n.tab-content {\n\tborder: 1px solid $border-color;\n\tborder-top: 0;\n\tpadding: 2rem 1rem;\n\ttext-align: justify;\n\t&.tab-content-custom-pill {\n\t\tborder: 0;\n\t\tpadding-left: 0;\n\t}\n}\n\n.tab-vertical {\n\t@extend .row;\n\t.nav-tabs {\n\t\t@extend .col-4;\n\t\t@extend .flex-column;\n\t\tborder-bottom: 0;\n\t\t.nav-link {\n\t\t\t&.active {\n\t\t\t\tborder-bottom-color: $border-color;\n\t\t\t}\n\t\t}\n\t\t.nav-item {\n\t\t\t&:first-child {\n\t\t\t\t.nav-link {\n\t\t\t\t\tborder-radius: 4px 4px 0 0;\n\t\t\t\t}\t\n\t\t\t}\n\t\t\t&:last-child {\n\t\t\t\t.nav-link {\n\t\t\t\t\tborder-radius: 0 0 4px 4px;\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n\t.tab-content {\n\t\t@extend .col-8;\n\t\tborder-top: 1px solid $border-color;\n\t}\n}\n\n.tab-vertical-custom {\n\t@extend .tab-vertical;\n\t.nav-tabs {\n\t\tbackground: theme-gradient-color(primary);\n\t\tborder-radius: 4px;\n\t\t@extend .col-3;\n\t\t.nav-link {\n\t\t\tbackground: transparent;\n\t\t\tborder-color: rgba($white, .2);\n\t\t\tcolor: darken(theme-color(primary), 30%);\n\t\t\tfont-family: $type1-bold;\n\t\t\tpadding: 1.75rem;\n\t\t\t&.active {\n\t\t\t\tcolor: $white;\n\t\t\t\tborder-color: rgba($white, .2);\n\t\t\t}\n\t\t}\n\t}\n\t&.tab-content {\n\t\t@extend .col-9;\n\t\tborder: 0;\n\t\tpadding-top: 0;\n\t}\n}","/* Dropdowns */\n\n.dropdown {\n  .dropdown-toggle {\n    &:after {\n      border-top: 0;\n      border-right: 0;\n      border-left: 0;\n      border-bottom: 0;\n      font: normal normal normal 24px/1 \"Material Design Icons\";\n      content: \"\\F0140\";  \n      width: auto;\n      height: auto;\n      vertical-align: baseline;\n      font-size: .75rem;\n    }\n  }\n  .dropdown-menu {\n    margin-top: .75rem;\n    font-size: $default-font-size;\n    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);    \n    .dropdown-item {\n      font-size: 1rem;\n      padding: .25rem 1.5rem;\n      cursor: pointer;\n      &:active {\n        background: initial;\n      }\n    }\n  }\n}\n","ngb-datepicker {\n  .ngb-dp-header {\n    @extend .bg-success;\n    ngb-datepicker-navigation {\n      padding: 35px 25px 20px 25px;\n      .ngb-dp-arrow {\n        .ngb-dp-arrow-btn {\n          .ngb-dp-navigation-chevron {\n            border-color: $white;\n          }\n        }\n        .rtl & {\n          transform: rotate(180deg);\n        }\n      }\n      .ngb-dp-navigation-select {\n        select {\n          &:first-child {\n            margin-right: 3px;\n            .rtl & {\n              margin-left: 3px;\n            }\n          }\n          &:last-child {\n            margin-left: 3px;\n          }\n          &.custom-select {\n            background-color: transparent;\n            border: 1px solid $white;\n            color: $white;\n            .rtl & {\n              padding-right: 45px;\n            }\n          }\n        }\n      }\n    }\n  }\n  .ngb-dp-months {\n    .ngb-dp-month {\n      width: 100%;\n      ngb-datepicker-month-view {\n        .ngb-dp-week {\n          display: flex;\n          @include justify-content(space-around);\n          &:last-child {\n            padding-bottom: 35px;\n            margin-bottom: 0;\n          }\n          .ngb-dp-day {\n            &.ngb-dp-today {\n              background: white;\n              border-radius: 50%;\n              .btn-light {\n                color: white;\n              }\n            }\n          }\n          &.ngb-dp-weekdays {\n            @extend .bg-info;\n            .ngb-dp-weekday {\n              color: $white;\n              font-style: normal;\n              font-weight: bold;\n            }\n          }\n          &:not(.ngb-dp-weekdays) {\n            margin-bottom: 20px;\n            &:last-child {\n              margin-bottom: 0;\n            }\n          }\n        }\n      }\n    }\n  }\n  .ngb-dp-week {\n    display: flex;\n    justify-content: space-between;\n  }\n  .ngb-dp-day {\n    height: 3rem;\n  }\n}","/* Pagination */\n\n.pagination{\n  .page-item {\n    .page-link {\n      border-color: $border-color;\n      color: color(black);\n      font-size: .875rem;\n      @include transition-duration(0.3s);\n      &:focus{\n        background: inherit;\n      }\n      i {\n        &:before {\n          font-size: inherit;\n          line-height: 1;\n          vertical-align: middle;\n        }\n      }\n    }\n    &.active,\n    &:hover,\n    &:focus,\n    &:active {\n      .page-link {\n        background: theme-color(\"primary\");\n        border-color: theme-color(\"primary\");\n        color: color(white);\n      }\n    }\n  }\n  &.flat{\n    .page-item{\n      .page-link{\n        border: none;\n        @include border-radius(2px);\n      }\n    }\n  }\n  &.separated{\n    .page-item{\n      margin-left: 2px;\n      margin-right: 2px;\n      &:first-child{\n        margin-left: 0;\n      }\n      &:last-child{\n        margin-right: 0;\n      }\n      .page-link{\n        @include border-radius(2px);\n      }\n    }\n  }\n  &.rounded{\n    .page-item{\n      .page-link{}\n      &:first-child{\n        .page-link{\n          @include border-radius(25px 0 0 25px);\n        }\n      }\n      &:last-child{\n        .page-link{\n          @include border-radius(0 25px 25px 0);\n        }\n      }\n    }\n  }\n  &.rounded-flat{\n    .page-item{\n      margin-right: 3px;\n      margin-left: 3px;\n      .page-link{\n        border: none;\n        @include border-radius(50px);\n\n      }\n    }\n  }\n  &.rounded-separated{\n    .page-item{\n      margin-left: 2px;\n      margin-right: 2px;\n      &:first-child{\n        margin-left: 0;\n        .page-link{\n          @include border-radius(10px 0 0 10px);\n        }\n      }\n      &:last-child{\n        margin-right: 0;\n        .page-link{\n          @include border-radius(0 10px 10px 0);\n        }\n      }\n      .page-link{\n        @include border-radius(2px);\n      }\n    }\n  }\n}\n\n\n/* pagination variations */\n@each $color, $value in $theme-colors {\n  .pagination-#{$color} {\n    @include pagination-variants($value);\n  }\n}","@import \"shared\";\n@import \"../functions\";\n\n// CSS Transitions\n// Currently only works in Webkit.\n//\n// * expected in CSS3, FireFox 3.6/7 and Opera Presto 2.3\n// * We'll be prepared.\n//\n// Including this submodule sets following defaults for the mixins:\n//\n//     $default-transition-property : all\n//     $default-transition-duration : 1s\n//     $default-transition-function : false\n//     $default-transition-delay    : false\n//\n// Override them if you like. Timing-function and delay are set to false for browser defaults (ease, 0s).\n\n$default-transition-property: all !default;\n\n$default-transition-duration: 1s !default;\n\n$default-transition-function: false !default;\n\n$default-transition-delay: false !default;\n\n$transitionable-prefixed-values: transform, transform-origin !default;\n\n// One or more properties to transition\n//\n// * for multiple, use a comma-delimited list\n// * also accepts \"all\" or \"none\"\n\n@mixin transition-property($property-1: $default-transition-property,\n  $property-2 : false,\n  $property-3 : false,\n  $property-4 : false,\n  $property-5 : false,\n  $property-6 : false,\n  $property-7 : false,\n  $property-8 : false,\n  $property-9 : false,\n  $property-10: false\n) {\n  @if type-of($property-1) == string { $property-1: unquote($property-1); }\n  $properties: compact($property-1, $property-2, $property-3, $property-4, $property-5, $property-6, $property-7, $property-8, $property-9, $property-10);\n  @if $experimental-support-for-webkit    {       -webkit-transition-property : prefixed-for-transition(-webkit, $properties); }\n  @if $experimental-support-for-mozilla   {          -moz-transition-property : prefixed-for-transition(-moz,    $properties); }\n  @if $experimental-support-for-opera     {            -o-transition-property : prefixed-for-transition(-o,      $properties); }\n                                                          transition-property : $properties;\n}\n\n// One or more durations in seconds\n//\n// * for multiple, use a comma-delimited list\n// * these durations will affect the properties in the same list position\n\n@mixin transition-duration($duration-1: $default-transition-duration,\n  $duration-2 : false,\n  $duration-3 : false,\n  $duration-4 : false,\n  $duration-5 : false,\n  $duration-6 : false,\n  $duration-7 : false,\n  $duration-8 : false,\n  $duration-9 : false,\n  $duration-10: false\n) {\n  @if type-of($duration-1) == string { $duration-1: unquote($duration-1); }\n  $durations: compact($duration-1, $duration-2, $duration-3, $duration-4, $duration-5, $duration-6, $duration-7, $duration-8, $duration-9, $duration-10);\n  @include experimental(transition-duration, $durations,\n    -moz, -webkit, -o, not(-ms), not(-khtml), official\n  );\n}\n\n// One or more timing functions\n//\n// * [ ease | linear | ease-in | ease-out | ease-in-out | cubic-bezier(x1, y1, x2, y2)]\n// * For multiple, use a comma-delimited list\n// * These functions will effect the properties in the same list position\n\n@mixin transition-timing-function($function-1: $default-transition-function,\n  $function-2 : false,\n  $function-3 : false,\n  $function-4 : false,\n  $function-5 : false,\n  $function-6 : false,\n  $function-7 : false,\n  $function-8 : false,\n  $function-9 : false,\n  $function-10: false\n) {\n  $function-1: unquote($function-1);\n  $functions: compact($function-1, $function-2, $function-3, $function-4, $function-5, $function-6, $function-7, $function-8, $function-9, $function-10);\n  @include experimental(transition-timing-function, $functions,\n    -moz, -webkit, -o, not(-ms), not(-khtml), official\n  );\n}\n\n// One or more transition-delays in seconds\n//\n// * for multiple, use a comma-delimited list\n// * these delays will effect the properties in the same list position\n\n@mixin transition-delay($delay-1: $default-transition-delay,\n  $delay-2 : false,\n  $delay-3 : false,\n  $delay-4 : false,\n  $delay-5 : false,\n  $delay-6 : false,\n  $delay-7 : false,\n  $delay-8 : false,\n  $delay-9 : false,\n  $delay-10: false\n) {\n  @if type-of($delay-1) == string { $delay-1: unquote($delay-1); }\n  $delays: compact($delay-1, $delay-2, $delay-3, $delay-4, $delay-5, $delay-6, $delay-7, $delay-8, $delay-9, $delay-10);\n  @include experimental(transition-delay, $delays,\n    -moz, -webkit, -o, not(-ms), not(-khtml), official\n  );\n}\n\n// Transition all-in-one shorthand\n\n@mixin single-transition(\n  $property: $default-transition-property,\n  $duration: $default-transition-duration,